<template>
  <div id="app">
    <div class="contain">
      <aside class="sidebar contain__sidebar">
        <app-logo></app-logo>

        <app-navigation></app-navigation>

        <app-footer></app-footer>
      </aside>

      <section class="page contain__page">
        <router-view></router-view>        
      </section>
    </div>
  </div>
</template>

<script>
import AppLogo from '@/components/modules/AppLogo'
import AppFooter from '@/components/modules/AppFooter'
import AppNavigation from '@/components/modules/AppNavigation'

export default {
  name: 'App',
  components: { AppLogo, AppNavigation, AppFooter },

  data() {
    return {}
  }
}
</script>














